import React from 'react'
import { NavLink } from 'react-router-dom'
import { PATH } from './Routes'

function Header() {
    return (
            <nav>
                <ul>
                    <li>
                        <NavLink to={PATH.PRE_JUNIOR}>Profile</NavLink>
                    </li>
                    <li>
                        <NavLink to={PATH.JUNIOR}>Message</NavLink>
                    </li>
                    <li>
                        <NavLink to={PATH.JUNIOR_PLUS}>Music</NavLink>
                    </li>
                </ul>
            </nav>
    )
}

export default Header
